<template>
  <v-dialog
    persistent
    :value="value"
  >
    <v-card>
      <v-card-title>{{ title }}</v-card-title>
      <v-card-subtitle>{{ subtitle }}</v-card-subtitle>
      <v-card-text>{{ text }}</v-card-text>
      <v-card-actions>
        <v-btn
          v-for="{ id, text } in buttons"
          :key="id"
          text
          @click="$emit(`${ id }`)"
        >
          {{ text }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    value: {
      required: true,
    },
    title: {
      required: true,
    },
    subtitle: {
      required: false,
    },
    text: {
      required: false,
    },
    buttons: {
      type: Array,
      required: true,
    }
  },
}
</script>