import Vue from 'vue'
import App from './App.vue'
import router from './router'
import vuetify from './plugins/vuetify'
import * as firebase from 'firebase'
import store from './store'
import i18n from './i18n'

Vue.config.productionTip = false

new Vue({
  router,
  vuetify,
  render: h => h(App),
  store,
  i18n,

  created () {
  // Your web app's Firebase configuration
  var firebaseConfig = {
    apiKey: "AIzaSyBuH0lkHiivAAFRvrYGAIRZTMUnXQrVIz8",
    authDomain: "milkana-konov.firebaseapp.com",
    databaseURL: "https://milkana-konov.firebaseio.com",
    projectId: "milkana-konov",
    storageBucket: "milkana-konov.appspot.com",
    messagingSenderId: "410781288066",
    appId: "1:410781288066:web:6f679061fb2f981d5dd69b",
    measurementId: "G-SZ6Q8ZLQGB"
  };
  // Initialize Firebase
  firebase.initializeApp(firebaseConfig);
  firebase.analytics();

  firebase.auth().onAuthStateChanged(user => {
    if (user) {
      store.dispatch('autoSignin', user);
    }
  });

  store.dispatch('paintings/fetchOnlinePaintings');
  }
}).$mount('#app')
