<template>
  <v-container>
    <div class="text-h6 text-uppercase">
      Nicht Online
    </div>

    <v-row justify="center">
      <v-col
        v-for="painting in stagedPaintings"
        :key="painting.id"
        cols="12"
        xl="3"
        lg="3"
        md="4"
        sm="6"
        xs="12"
      >
        <staged-painting-card :painting="painting" />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import StagedPaintingCard from '../components/manage/gallery/StagedPaintingCard.vue'

export default {
  name: 'Gallery',
  components: {
    StagedPaintingCard
  },
  computed: {
    ...mapGetters('paintings', ['loading', 'pushing', 'success', 'error', 'stagedPaintings', 'onlinePaintings']),
  },
  created () {
    this.fetchStagedPaintings()
    this.fetchOnlinePaintings()
  },
  methods: {
    ...mapActions('paintings', ['fetchStagedPaintings', 'fetchOnlinePaintings']),
  }
}
</script>