import * as firebase from 'firebase'

const state = {
  loading: 0,
  pushing: 0,
  success: null,
  error: null,

  user: null,

  paintings: [],
  addedPainting: {},
};

const getters = {
  loading: state => state.loading > 0,
  pushing: state => state.pushin > 0,
  success: state => state.success,
  error: state => state.error,

  user: state => state.user,

  addedPainting: state => state.addedPainting,
};

const actions = {
  clearSuccess({commit}) {
    commit('clearSuccess')
  },
  clearError({commit}) {
    commit('clearError')
  },

  async signin({commit}, payload) {
    commit('startPushing')
    commit('clearError')
    try {
      let response = await firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
      commit('setUser', response.user)
      commit('setSuccess', response.user)
      commit('finishedPushing')
    } catch (error) {
      console.log(error)
      commit('setError', error)
      commit('finishedPushing')
    }
  },

  autoSignin({commit}, payload) {
    commit('setUser', payload)
  },


  fetchPaintings({commit}) {
    commit('clearSuccess')
    commit('clearError')
    firebase.database().ref('paintings').once('value')
      .catch(
        error => {
          console.log(error)
          commit('setError', error)
          commit('finishedLoading')
        }
      )
      .then(
        response => {
          var paintings = []
          response.forEach(element => {
            var item = element.val()
            item.id = element.key
            paintings.push(item)
          })
          commit('setPaintings', paintings)
          commit('finishedLoading')
        }
      )
  },

  addPainting({commit}, painting) {
    commit('clearSuccess')
    commit('clearError')
    firebase.database().ref('paintings').push(painting)
      .catch(
        error => {
          console.log(error)
          commit('setError', error)
          commit('finishedLoading')
        }
      )
      .then(
        response => {
          painting.id = response.key
          commit('setAddedPainting', painting)
          commit('finishedLoading')
        }
      )
  },

  updatePainting({commit}, painting) {
    firebase.database().ref(`/paintings/${ painting.id }`).update(painting)
    .catch(
      error => {
        console.log(error)
        commit('setError', error)
        commit('finishedLoading')
      }
    )
    .then( function () {
      commit('setAddedPainting', painting)
      commit('finishedLoading')
    }) 
  },

  addImagesToPainting({commit}, images) {
    commit('clearSuccess')
    commit('clearError')

    var i = state.addedPainting.imageURLs.length
    images.forEach(image => {
      // Create upload task
      var uploadTask = firebase.storage().ref(
        `images/${ state.addedPainting.id }_${ i }`
      )
      .put(image)

      uploadTask.on('state_changed',
        // Progress
        snapshot => {
          var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          console.log('Upload is ' + progress + '% done');
          switch (snapshot.state) {
            case firebase.storage.TaskState.PAUSED: // or 'paused'
              console.log('Upload is paused');
              break;
            case firebase.storage.TaskState.RUNNING: // or 'running'
              console.log('Upload is running');
              break;
          }
        },

        // Error
        error => {
          console.log(error)
          commit('setError', error)
          commit('finishedLoading')
        },

        // Finished
        () => {
          uploadTask.snapshot.ref.getDownloadURL().then(url => {
            var item = state.addedPainting
            item.imageURLs.push(url)

            firebase.database().ref(`/paintings/${ item.id }`).update(item)
            .catch(
              error => {
                console.log(error)
                commit('setError', error)
                commit('finishedLoading')
              }
            )
            .then( function () {
              commit('setAddedPainting', item)
              commit('finishedLoading')
            })
          })
        }
      )

      i++
    })
  },

  
};

const mutations = {
  startLoading: state => state.loading++,
  finishedLoading: state => state.loading--,
  startPushing: state => state.pushing++,
  finishedPushing: state => state.pushing--,
  setSuccess: (state, success) => state.success = success,
  clearSuccess: state => state.success = null,
  setError: (state, error) => state.error = error,
  clearError: state => state.error = null,

  setUser: (state, payload) => state.user = payload,

  setPaintings: (state, paintings) => state.paintings = paintings,

  setAddedPainting: (state, addedPainting) => state.addedPainting = addedPainting,
};

export default {
  state,
  getters,
  actions,
  mutations
};