<template>
  <div>
    <v-card>
      <v-card-text>
        <v-row>
          <v-col>
            <v-file-input
              v-model="images"
              accept="image/png, image/jpeg"
              chips
              counter
              label="Bilder Auswählen"
              multiple
              prepend-icon="mdi-folder-multiple-image"
              show-size
              @change="addImagesToStagedPainting"
            ></v-file-input>
          </v-col>
        </v-row>

        <v-row
          v-if="editPainting.imageURLs.length > 0"
        >
          <v-col>
            Bitte legen Sie die Reihenfolge der Bilder fest. Das als erstes angewählte Bild wird als Vorschaubild verwendet. 
          </v-col>
        </v-row>

        <v-row
          v-if="editPainting.imageURLs.length > 0"
        >
          <v-col>
            <v-btn
              color="accent"
              :disabled="sortedImageURLs.length < 1"
              text
              @click="sortedImageURLs = []"
            >
              Neu Anordnen
            </v-btn>
          </v-col>
        </v-row>

        <v-row>
          <v-col
            v-for="{ url, number } in addedImages"
            :key="url"
            cols="12"
            lg="2"
            sm="4"
            class="pb-8"
          >
            <v-card>

              <v-img
                contain
                :src="url"
                :lazy-src="url"
              >
                <template v-slot:placeholder>
                  <v-row
                    class="fill-height ma-0"
                    align="center"
                    justify="center"
                  >
                    <v-progress-circular indeterminate></v-progress-circular>
                  </v-row>
                </template>
              </v-img>

              <v-btn
                v-if="number === undefined"
                absolute
                bottom
                fab
                small
                right
                :value="url"
                @click="pushImage(url)"
              > 
                <v-icon>mdi-plus</v-icon>
              </v-btn>

              <v-btn
                v-else
                absolute
                bottom
                exact
                fab
                small
                right
              > 
                {{ number + 1 }}
              </v-btn>
            </v-card>
          </v-col>
        </v-row>
      </v-card-text>

      <v-card-actions>
        <v-btn
          color="accent"
          :disabled="nextDisabled"
          text
          :loading="pushing"
          @click="onNext"
        >
          Weiter
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import { cloneDeep } from 'lodash'
import { mapActions, mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters('paintings', ['loading', 'pushing', 'success', 'error', 'editPainting']),

    addedImages () {
      var items = [];
      this.editPainting.imageURLs.forEach(e => {
        items.push({
          url: e,
          number: this.imageNumber(e),
        });
      })
      return items;
    },

    nextDisabled () {
      return (this.sortedImageURLs.length !== this.editPainting.imageURLs.length) || this.sortedImageURLs.length === 0;
    },
  },
  created () {
    this.sortedImageURLs = cloneDeep(this.editPainting.imageURLs);
  },
  data () {
    return {
      images: [],
      sortedImageURLs: [],
    }
  },
  methods: {
    ...mapActions('paintings', ['clearSuccess', 'clearError' ,'addImagesToStagedPainting', 'updateStagedPainting']),

    pushImage (imageURL) {
      this.sortedImageURLs.push(imageURL);
    },

    imageNumber (imageURL) {
      const index = this.sortedImageURLs.findIndex(element => element === imageURL);
      if (index === -1) {
        return;
      }
      return index;
    },

    async onNext () {
      var updatedPainting = this.editPainting;
      updatedPainting.imageURLs = this.sortedImageURLs;
      await this.updateStagedPainting(updatedPainting);
      this.$emit('next');
    }
  },
  props: {

  },
}
</script>