<template>
  <v-container>
    <v-row justify="center">
      <v-col
        v-for="painting in onlinePaintings"
        :key="painting.id"
        cols="12"
        xl="3"
        lg="3"
        md="4"
        sm="6"
        xs="12"
      >
        <painting-card :painting="painting" />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import PaintingCard from '../components/gallery/PaintingCard.vue'

export default {
  name: 'Gallery',
  components: {
    PaintingCard
  },
  computed: {
    ...mapGetters('paintings', ['loading', 'pushing', 'success', 'error', 'onlinePaintings']),
  },
  created () {
    this.fetchOnlinePaintings();
  },
  methods: {
    ...mapActions('paintings', ['fetchOnlinePaintings']),
  }
}
</script>