<template>
  <v-card>
    <v-img
      :contain="image.contain"
      :height="imageHeight"
      :src="image.src"
      @click="$router.push(link)"
    >
    </v-img>

    <v-card-title>{{ locale.title }}</v-card-title>

    <v-card-actions>
      <v-btn
        color="accent"
        text
        :to="link"
      >
        Verwalten
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  computed: {
    imageHeight () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xl': return 600
        case 'lg': return 360
        case 'md': return 360
        case 'sm': return 420
        case 'xs': return 520
        default: return 360
      }
    },

    image () {
      return {
        src: this.painting.imageURLs !== undefined ? this.painting.imageURLs[0] : require('@/assets/no-image.png'),
        contain: this.painting.imageURLs === undefined
      }
    },

    locale () {
      if (this.painting.locales !== undefined) { 
        return this.painting.locales.find(locale => locale.locale === this.$i18n.locale)
      } else {
        return {
          title: 'Unbenannt',
          description: '',
        }
      }
    },

    link () {
      return `/gallery/${this.painting.id}`
    }
  },
  props: {
    painting: {
      type: Object,
      required: true
    }
  }
}
</script>