<template>
  <v-container>
    <dialog-card
      title="Das Gemälde wurde erfolgreich hinzugefügt."
      :buttons="[ { id: 'ok', text: 'OK' } ]"
      :value="finishDialog"
      @ok="leavePage"
    />

    <v-stepper
      v-model="step"
      vertical
    >
      <v-stepper-step :complete="step > 1" step="1">Bilder</v-stepper-step>
      <v-stepper-content step="1">
        <add-images-card 
          v-if="this.editPainting.imageURLs !== undefined"
          @next="step++"
        />
      </v-stepper-content>

      <v-stepper-step :complete="step > 2" step="2">Allgemeine Daten</v-stepper-step>
      <v-stepper-content step="2">
        <add-general-data-card 
          @next="step++"
        />
      </v-stepper-content>

      <v-stepper-step :complete="step > 3" step="3">Bulgarische Beschreibung</v-stepper-step>
      <v-stepper-content step="3">
        <add-texts-card 
          locale="bg"
          @next="step++"
        />
      </v-stepper-content>

      <v-stepper-step :complete="step > 4" step="4">Deutsche Beschreibung</v-stepper-step>
      <v-stepper-content step="4">
        <add-texts-card
          locale="de"
          @next="step++"
        />
      </v-stepper-content>

      <v-stepper-step :complete="step > 5" step="5">Englische Beschreibung</v-stepper-step>
      <v-stepper-content step="5">
        <add-texts-card
          locale="en"
          @next="step++"
        />
      </v-stepper-content>

      <v-stepper-step :complete="step > 6" step="6">Französische Beschreibung</v-stepper-step>
      <v-stepper-content step="6">
        <add-texts-card
          locale="fr"
          @next="finishPainting"
        />
      </v-stepper-content>
    </v-stepper>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import AddImagesCard from '../components/manage/AddImagesCard.vue'
import AddGeneralDataCard from '../components/manage/AddGeneralDataCard.vue'
import AddTextsCard from '../components/manage/AddTextsCard.vue'
import DialogCard from '../components/DialogCard.vue'

export default {
  components: {
    AddImagesCard,
    AddGeneralDataCard,
    AddTextsCard,
    DialogCard,
  },
  computed: {
    ...mapGetters('paintings', ['loading', 'pushing', 'success', 'error' ,'editPainting']),
  },
  created () {
    this.addPainting({
      id: '',
      imageURLs: [],
      data: {
        editDate: new Date().toISOString(),
        creationDate: new Date().toISOString(),
      },
      locales: [],
    });
  },
  data () {
    return {
      step: 1,

      finishDialog: null,
    }
  },
  methods: {
    ...mapActions('paintings', ['addPainting', 'applyStagedPainting']),

    finishPainting () {
      this.applyStagedPainting();
      this.finishDialog = 1;
    },

    leavePage () {
      this.finishDialog = null,
      this.$router.push('/manage/gallery')
    }
  },
}
</script>