<template>
  <v-container>
    <v-row justify="center">
      <v-col
        cols="12"
        xl="6"
        lg="6"
        md="12"
        sm="12"
        xs="12"
      > 
        <v-skeleton-loader
          v-if="loading" 
          type="image"
        />
          
        <painting-carousel-card
          v-else
          :images="painting.imageURLs"
        />
      </v-col>

      <v-col
        cols="12"
        xl="6"
        lg="6"
        md="12"
        sm="12"
        xs="12"
      >
        <v-skeleton-loader
          v-if="loading" 
          type="card"
        />
          
        <painting-information-card
          v-else
          :painting="painting"
        />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import PaintingCarouselCard from '../components/paintingDetail/PaintingCarouselCard.vue'
import PaintingInformationCard from '../components/paintingDetail/PaintingInformationCard.vue'

export default {
  components: {
    PaintingCarouselCard,
    PaintingInformationCard
  },
  computed: {
    ...mapGetters('paintings', ['loading', 'pushing', 'success', 'error', 'onlinePaintings']),

    carouselHeight () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xl': return 700
        case 'lg': return 480
        case 'md': return 480
        case 'sm': return 450
        case 'xs': return 560
        default: return 360
      }
    },

    painting () {
      return this.onlinePaintings.find(painting => painting.id === this.$route.params.id)
    }
  },
  created () {
    this.fetchOnlinePaintings();
  },
  methods: {
    ...mapActions('paintings', ['fetchOnlinePaintings']),
  }
}
</script>
