<template>
  <div>
    <v-card>
      <v-form
        v-model="valid"
      >
        <v-card-text>
          <v-row>
            <v-col
              cols="12"
              lg="6"
              sm="12"
            >
              <v-select
                v-model="kind"
                label="Art"
                prepend-icon="mdi-information"
                :items="kinds"
                required
              ></v-select>
            </v-col>
            <v-col
              cols="12"
              lg="6"
              sm="12"
            >
              <v-select
                v-model="surface"
                label="Untergrund"
                prepend-icon="mdi-square"
                :items="surfaces"
                required
              ></v-select>
            </v-col>
            <v-col
              cols="12"
              lg="6"
              sm="12"
            >
              <v-select
                v-model="frame"
                label="Rahmen"
                prepend-icon="mdi-crop-square"
                :items="frames"
                required
              ></v-select>
            </v-col>
          </v-row>
          <v-row>
            <v-col
              cols="12"
              lg="6"
              sm="12"
            >
              <v-text-field
                v-model="height"
                label="Höhe"
                prepend-icon="mdi-arrow-split-horizontal"
                :rules="sizeRules"
                suffix="cm"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              lg="6"
              sm="12"
            >
              <v-text-field
                v-model="width"
                label="Breite"
                prepend-icon="mdi-arrow-split-vertical"
                :rules="sizeRules"
                suffix="cm"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        
        <v-card-actions>
          <v-btn
            color="accent"
            :disabled="!valid"
            text
            :loading="pushing"
            @click="onNext"
          >
            Weiter
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </div>
</template>

<script>
//import { cloneDeep } from 'lodash'
import { mapActions, mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters('paintings', ['loading', 'pushing', 'success', 'error', 'editPainting']),
  },
  created () {
    //this.kind = 
  },
  data () {
    return {
      kinds: [
        { value: 'oil', text: 'Ölgemälde' },
        { value: 'watercolor', text: 'Aquarell Gemälde' },
      ],
      surfaces: [
        { value: 'canvas', text: 'Leinwand' },
        { value: 'paper', text: 'Papier' },
      ],
      frames: [
        { value: 'frameless', text: 'Rahmenlos' },
        { value: 'wooden', text: 'Holzrahmen' },
      ],

      kind: 'oil',
      surface: 'canvas',
      frame: 'frameless',
      height: null,
      width: null,

      sizeRules: [
        v => !!v || 'Größe wird benötigt',
        v => !isNaN(v) || 'Buchstaben vermeiden',
        v => !(parseFloat(v) <= 0 || parseFloat(v) > 300) || 'Ungültige Größe',
      ],

      valid: false,
    }
  },
  methods: {
    ...mapActions('paintings', ['clearSuccess', 'clearError', 'updateStagedPainting']),

    async onNext () {
      var data = {
        kind: this.kind,
        surface: this.surface,
        frame: this.frame,
        height: this.height,
        width: this.width,
      };

      var updatedPainting = this.editPainting;
      updatedPainting.data = data;

      await this.updateStagedPainting(updatedPainting);
      this.$emit('next');
    }
  },
  props: {

  },
}
</script>