import * as firebase from 'firebase'

const state = {
  loading: 0,
  pushing: 0,
  success: null,
  error: null,

  onlinePaintings: [],
  stagedPaintings: [],
  editPainting: {},
};

const getters = {
  loading: state => state.loading > 0,
  pushing: state => state.pushing > 0,
  success: state => state.success,
  error: state => state.error,

  onlinePaintings: state => state.onlinePaintings,
  stagedPaintings: state => state.stagedPaintings,
  editPainting: state => state.editPainting,
};

const actions = {
  clearSuccess({commit}) {
    commit('clearSuccess')
  },
  clearError({commit}) {
    commit('clearError')
  },

  // Fetch
  async fetchOnlinePaintings({commit}) {
    commit('startLoading')
    try {
      let response = await firebase.database().ref('paintings/online').once('value')
      var paintings = []
      response.forEach(e => {
        var item = e.val()
        item.id = e.key
        paintings.push(item)
      })
      commit('setOnlinePaintings', paintings)
      commit('finishedLoading')
    } catch (err) {
      console.log(err)
      commit('setError', err)
      commit('finishedLoading')
    }
  },

  async fetchStagedPaintings({commit}) {
    commit('startLoading')
    try {
      let response = await firebase.database().ref('paintings/staged').once('value')
      var paintings = []
      response.forEach(e => {
        var item = e.val()
        item.id = e.key
        paintings.push(item)
      })
      commit('setStagedPaintings', paintings)
      commit('finishedLoading')
    } catch (err) {
      console.log(err)
      commit('setError', err)
      commit('finishedLoading')
    }
  },

  // Remove
  async removeOnlinePainting({commit}, painting) {
    commit('startPushing')
    try {
      await firebase.database().ref(`/paintings/online/${ painting.id }`).remove()
      commit('finishedPushing')
      this.fetchOnlinePaintings()
    } catch (err) {
      console.log(err)
      commit('setError', err)
      commit('finishedPushing')
    }
  },

  async removeStagedPainting({commit}, painting) {
    commit('startPushing')
    try {
      await firebase.database().ref(`/paintings/staged/${ painting.id }`).remove()
      commit('finishedPushing')
      this.fetchStagedPaintings()
    } catch (err) {
      console.log(err)
      commit('setError', err)
      commit('finishedPushing')
    }
  },

  // Add edit painting
  async addPainting({commit}, painting) {
    commit('startPushing')
    try {
      let response = await firebase.database().ref('paintings/staged').push(painting)
      painting.id = response.key
      commit('setEditPainting', painting)
      commit('finishedPushing')
    } catch (err) {
      console.log(err)
      commit('setError', err)
      commit('finishedPushing')
    }
  },

  // Set edit painting
  async updateStagedPainting({commit}, painting) {
    commit('setEditPainting', painting)
  },

  // Apply edit painting
  async applyStagedPainting({commit}) {
    commit('startPushing')
    try {
      await firebase.database().ref(`/paintings/staged/${ state.editPainting.id }`).update(state.editPainting)
      commit('finishedPushing')
    } catch (err) {
      console.log(err)
      commit('setError', err)
      commit('finishedPushing')
    }
  },

  // Add images to edit painting
  async addImagesToStagedPainting({commit}, images) {
    commit('startPushing')

    // Delete already uploaded images
    if (state.editPainting.imageURLs.length > 0) {
      const promises = []
      state.editPainting.imageURLs.forEach((e, i) => {
        promises.push(
          firebase.storage().ref(`/images/${ state.editPainting.id }_${ i }`).delete()
        )
      })
      try {
        await Promise.all(promises)
        // ToDo: mutation nehmen
        state.editPainting.imageURLs = []
      } catch (err) {
        console.log(err)
        commit('setError', err)
        commit('finishedPushing')
        return
      }
    }

    // Upload images to storage
    const uploadPromises = []
    var i = 0
    images.forEach(image => {
      uploadPromises.push(
        firebase.storage().ref(`images/${ state.editPainting.id }_${ i }`).put(image)
      )
      i++
    })
    try {
      await Promise.all(uploadPromises)
    } catch (err) {
      console.log(err)
      commit('setError', err)
      commit('finishedPushing')
      return
    }

    // Get URLs of uploaded images
    const urlPromises = []
    uploadPromises.forEach(res => {
      urlPromises.push(res.snapshot.ref.getDownloadURL())
    })
    let urlResponses = []
    try {
      urlResponses = await Promise.all(urlPromises)
    } catch (err) {
      console.log(err)
      commit('setError', err)
      commit('finishedPushing')
      return
    }

    // Update painting
    // ToDo: Bereits angelegte mutations und actions dazu nehmen
    var painting = state.editPainting
    urlResponses.forEach(res => {
      painting.imageURLs.push(res)
    })
    try {
      await firebase.database().ref(`/paintings/staged/${ painting.id }`).update(painting)
      commit('setEditPainting', painting)
      commit('finishedPushing')
    } catch (err) {
      console.log(err)
      commit('setError', err)
      commit('finishedPushing')
    }
  },
};

const mutations = {
  startLoading: state => state.loading++,
  finishedLoading: state => state.loading--,
  startPushing: state => state.pushing++,
  finishedPushing: state => state.pushing--,
  setSuccess: (state, success) => state.success = success,
  clearSuccess: state => state.success = null,
  setError: (state, error) => state.error = error,
  clearError: state => state.error = null,

  setOnlinePaintings: (state, paintings) => state.onlinePaintings = paintings,
  setStagedPaintings: (state, paintings) => state.stagedPaintings = paintings,
  setEditPainting: (state, painting) => state.editPainting = painting,
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};