<template>
  <div>
    <v-card>
      <v-form
        v-model="valid"
      >
        <v-card-text>
          <v-row>
            <v-col>
              <v-text-field
                v-model="title"
                counter
                label="Titel"
                prepend-icon="mdi-format-title"
                :rules="titleRules"
              ></v-text-field>
            </v-col>
          </v-row>

          <v-row>
            <v-col>
              <v-textarea
                v-model="description"
                counter
                label="Beschreibung"
                prepend-icon="mdi-text-subject"
                :rules="descriptionRules"
              ></v-textarea>
            </v-col>
          </v-row>
        </v-card-text>
        
        <v-card-actions>
          <v-btn
            color="accent"
            :disabled="!valid"
            text
            :loading="pushing"
            @click="onNext"
          >
            Weiter
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters('paintings', ['loading', 'pushing', 'success', 'error', 'editPainting']),
  },
  data () {
    return {
      title: '',
      description: '',

      titleRules: [
        v => !!v || 'Titel wird benötigt',
        v => v.length <= 25 || 'Maximal 25 Buchstaben',
      ],
      descriptionRules: [
        v => v.length <= 500 || 'Maximal 500 Buchstaben',
      ],

      valid: false,
    }
  },
  methods: {
    ...mapActions('paintings', ['clearSuccess', 'clearError', 'updateStagedPainting']),

    async onNext () {
      var locale = {
        locale: this.locale,
        title: this.title,
        description: this.description,
      };
      var updatedPainting = this.editPainting;
      updatedPainting.locales.push(locale);

      await this.updateStagedPainting(updatedPainting);
      this.$emit('next');
    }
  },
  props: {
    locale: {
      type: String,
      required: true,
    },
  },
}
</script>