<template>
  <v-container>

    <DialogCard
      :buttons="[ { id: 'dismiss', text: 'Okay' } ]"
      title="Oops... Da ist wohl etwas schief gelaufen."
      subtitle="Ihre Anmeldedaten scheinen nicht richtig zu sein."
      :text="error"
      :value="error" 
      @dismiss="clearError" 
    />

    <DialogCard
      :buttons="[ { id: 'dismiss', text: 'Wunderbar' } ]"
      title="Sie sind nun angemeldet."
      :value="success" 
      @dismiss="clearSuccess; $router.push('/manage')" 
    />

    <v-row
      justify="center"
    >
      <v-col
        cols="12"
        lg="6"
        md="8"
        sm="10"
      >
        <v-card>
          <v-card-title>Anmelden</v-card-title>
          <v-card-text>
            <v-form
              ref="form"
              v-model="valid"

            >
              <v-text-field
                v-model="email"
                label="E-Mail"
                :rules="emailRules"
              ></v-text-field>

              <v-text-field
                v-model="password"
                label="Passwort"
                type="password"
                :rules="passwordRules"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn
              :disabled="!valid"
              :loading="pushing"
              @click="signin({ email: email, password: password })"
            >
              Anmelden
            </v-btn>  
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import DialogCard from '../components/DialogCard'

export default {
  components: {
    DialogCard,
  },
  computed: {
    ...mapGetters(['loading', 'pushing', 'success', 'error', 'user']),
  },
  data () {
    return {
      valid: true,
      email: '',
      emailRules: [
        v => !!v || 'E-Mail wird benötigt',
        v => /.+@.+\..+/.test(v) || 'E-mail muss gültig sein',
      ],
      password: '',
      passwordRules: [
        v => !!v || 'Passwort wird benötigt',
        v => (v && v.length >= 6) || 'Passwort muss mindestens 6 Zeichen haben'
      ]
    }
  },
  methods: {
    ...mapActions(['clearSuccess', 'clearError', 'signin']),
  },
  /*
  watch: {
    user (v) {
      if (v !== null && v !== undefined) {
        this.$router.push('/manage')
      }
    },
  },
  */
}
</script>