import store from '../store'

export const preventUnauthenticated = (to, from, next) => {
  if (store.getters.user === null || store.getters.user === undefined) {
    next('/manage/signin')
  } else {
    next()
  }
}

export const preventAuthenticated = (to, from, next) => {
  if (store.getters.user) {
    next('/manage')
  } else {
    next()
  }
}