<template>
  <v-card>
    <v-img
      :src="painting.imageURLs[0]"
      :height="imageHeight"
      @click="$router.push(link)"
    >
    </v-img>

    <v-card-title>{{ locale.title }}</v-card-title>

    <v-card-actions>
      <v-btn
        color="accent"
        text
        :to="link"
      >
        {{ $t('gallery.more-button') }}
      </v-btn>

      <v-spacer></v-spacer>

      <v-btn
        icon
      >
        <v-icon>mdi-heart</v-icon>
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  computed: {
    imageHeight () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xl': return 600
        case 'lg': return 360
        case 'md': return 360
        case 'sm': return 420
        case 'xs': return 520
        default: return 360
      }
    },

    locale () {
      return this.painting.locales.find(locale => locale.locale === this.$i18n.locale)
    },

    link () {
      return `/gallery/${this.painting.id}`
    }
  },
  props: {
    painting: {
      type: Object,
      required: true
    }
  }
}
</script>