<template>
  <v-sheet elevation="2">
    <v-carousel
      cycle
      :height="carouselHeight"
      hide-delimiter-background
      show-arrows-on-hover
      interval="10000"
    >
      <v-carousel-item
        v-for="image in images"
        :key="image"
        :src="image"
        contain
        reverse-transition="slide-x-transition"
        transition="slide-x-transition"
      />
    </v-carousel>
  </v-sheet>  
</template>

<script>
export default {
  computed: {
    carouselHeight () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xl': return 700
        case 'lg': return 480
        case 'md': return 480
        case 'sm': return 450
        case 'xs': return 560
        default: return 360
      }
    }
  },
  props: {
    /**
     * Array with links to images which should be in carousel
     */
    images: {
      type: Array,
      required: true
    }
  }
}
</script>
